@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Spectral:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap);
.NavMain {
  position: -webkit-sticky;
  position: sticky;
  background-color: transparent;
  left: 0;
  right: 0;
  color: rgb(57, 57, 57);
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
}

.logo-green {
  color: #005a09;
}

.logo-peach {
  color: #e28277;
}

.NavMain .logo {
  font-family: Spectral;
  font-size: 2.2rem;
  font-weight: 600;
  margin: 1.2rem;
  z-index: 1;
}

.NavMain ul {
  display: flex;
  align-items: center;
  margin-left: auto;
  font-weight: 600;
  font-size: 1.2rem;
}

.NavMain li {
  font-family: Spectral;
  cursor: pointer;
  font-size: 1.2rem;
  margin: 0 1.5rem;
  font-weight: 600;
}

.NavMain li:hover {
  -webkit-text-decoration: underline #e28277;
          text-decoration: underline #e28277;
  color: #e28277;
}

.NavMain li svg {
  margin-right: 0.5rem;
  -webkit-filter: invert(43%) sepia(88%) saturate(3991%) hue-rotate(224deg)
    brightness(98%) contrast(107%);
          filter: invert(43%) sepia(88%) saturate(3991%) hue-rotate(224deg)
    brightness(98%) contrast(107%);
  position: relative;
  top: 0.4rem;
}

.NavMain ul li:not(:last-child) {
  margin-right: 1.5em;
}

.card-container {
  display: flex;
}

.left-column {
  flex-direction: column;
  width: 50%;
}

.CardItems {
  text-align: center;
  margin-top: 20px;
}

.CardItem {
  min-width: 500px;
  margin-top: 50px;
  display: flex;
  padding: 20px 40px;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  background: white;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.CardItem .round-image {
  height: 100px;
  width: 100px;
  margin-left: 10px;
  /* margin: 0 auto; */
  border-radius: 100%;
  overflow: hidden;
  position: relative;
}

.CardItem .item-empty {
  margin-top: 60px;
  display: flex;
  padding: 40px 40px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.CardItem .item-empty button {
  margin-top: 20px;
  width: 100%;
  background: #5663ff;
  color: white;
  font-size: 16px;
  padding: 12px 16px;
  border-radius: 10px;
  border: none;
}

.CardItem .round-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.CardItem .description {
  flex: 2 1;
  text-align: center;
}

.CardItem .description h2 {
  margin: 10px 0;
}
.CardItem .description h2 {
  margin: 5px 0;
  font-weight: 400;
}

.CardItem .buttons {
  display: flex;
}

.CardItem > h3 {
  text-align: center;
}

.cardsContainer {
  display: flex;
  justify-content: center;
}

.container {
  width: 50%;
  margin: 20px 0;
}

.Btn {
  background: #5663ff;
  width: 30%;
  color: white;
  padding: 16px 0;
  font-size: 16px;
  border-radius: 10px;
  border: none;
  outline: none;
  cursor: pointer;
}

.Btn:active {
  transform: translate(1px, 1px);
}

.Btn.btn-primary {
  background: #5663ff;
  color: white;
}

.Btn.btn-primary:disabled {
  background: rgb(190, 190, 190);
}

.Btn:disabled {
  background: rgb(190, 190, 190);
}

.Btn.btn-secondary {
  background: white;
  color: #5663ff;
  border: 1px solid #5663ff;
}

.Profile {
  max-width: 800px;
  margin: 10vh auto 0;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
}

.Profile .user-contact {
  margin-top: 20px;
  background: #f1f2ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 10px;
}

.Profile .title {
  margin: 10px 0;
}

.Profile .stack.empty {
  margin-top: 60px;
  display: flex;
  padding: 40px 40px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.Profile .stack.empty button {
  margin-top: 20px;
  width: 100%;
  background: #5663ff;
  color: white;
  font-size: 16px;
  padding: 12px 16px;
  border-radius: 10px;
  border: none;
}

.Profile .user-image {
  margin: 0px auto;
  height: 200px;
  width: 200px;
}

.Profile .user-image.round-image {
  height: 250px;
  width: 250px;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
}

.Profile .user-presentation {
  text-align: center;
}

.Profile .user.contact {
  margin-top: 20px;
  background: #f1f2ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 10px;
}

.Profile .user.contact h4 {
  text-align: center;
  margin-top: 0;
}

.Profile .form-group .input {
  margin-top: 8px;
  width: 350px;
  border-radius: 4px;
  display: block;
  border: 1px solid #5663ff;
  font-size: 16px;
  background: transparent;
  padding: 12px 12px;
}

.Profile .form-group .label {
  font-size: 14px;
  font-weight: 600;
}

/* Section Banner */

.home-page .banner {
  display: flex;
}

.home-page .banner .text-wrapper {
  display: flex;
  color: white;
  flex: 1 1;
  flex-direction: column;
}

.home-page .banner .text-container {
  background-color: #005a09;
  padding: 40px;
}

.home-page .banner .text-wrapper .title {
  font-family: "spectral";
  font-weight: 600;
  font-size: 50px;
  margin-bottom: 30px;
}

.home-page .banner .text-wrapper .subtitle {
  font-family: "inter";
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 40px;
}

.home-page a:not(.external-link) {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #e28277;
  padding: 16px 60px;
  border-radius: 210px;
  font-family: "spectral";
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: 1px;
  color: white;
  cursor: pointer;
}

.home-page .img-wrapper {
  display: flex;
  display: none;
  flex: 0 1 auto;
  max-width: 30%;
}

.home-page .img-wrapper img {
  width: 100%;
  height: auto;
}

/* Section Columns */

.columns-wrapper {
  padding: 50px 30px;
}

.columns-wrapper p {
  font-family: "inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #16162b;
}

.columns-wrapper .column-title {
  font-family: "spectral";
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #e28277;
  margin-bottom: 30px;
}

/* Section Help */

.help-wrapper {
  background: #005a09;
  position: relative;
}

.help-wrapper .text-wrapper {
  padding: 40px 30px;
  position: relative;
  z-index: 1;
}

.help-wrapper p {
  font-family: "inter";
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: white;
  padding-bottom: 30px;
  max-width: 640px;
}

.help-wrapper .home {
  width: 300px;
  height: auto;
  position: absolute;
  z-index: 0;
}

.help-wrapper .home-1 {
  top: 100px;
  left: 20px;
  transform: rotate(26.25deg);
}

.help-wrapper .home-2 {
  top: 10px;
  left: 330px;
}

/* Ressources title */

.ressources-wrapper {
  padding: 50px 30px;
}

.ressources-wrapper .external-link {
  display: block;
}

.ressources-wrapper .external-link p {
  font-family: "inter";
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  margin-top: 10px;
  color: #e28277;
}

.ressources-wrapper .thumbnail-wrapper {
  width: 100%;
  display: block;
  overflow: hidden;
  position: relative;
}

.thumbnail-container {
  width: 100%;
  height: 100%;
  position: absolute;
}

.ressources-wrapper .thumbnail-wrapper img {
  width: 100%;
}

.ressources-wrapper .thumbnail-wrapper:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

/* Main title */

p.main-title {
  font-family: "spectral";
  font-weight: 700;
  font-size: 48px;
  line-height: 73px;
  padding-bottom: 30px;
}

p.main-title.green {
  color: #005a09;
  max-width: 670px;
  margin: 0 auto;
  text-align: center;
}

p.main-title.peach {
  color: #e28277;
}

@media (min-width: 1280px) {
  .home-page .banner .text-container {
    padding: 60px;
  }

  .home-page .banner .text-wrapper .title {
    font-size: 50px;
  }

  .home-page .img-wrapper {
    display: block;
  }

  .columns-wrapper {
    padding: 60px 30px 80px 30px;
  }

  .columns-wrapper .title {
    padding-bottom: 50px;
  }

  .help-wrapper .text-wrapper {
    padding-left: 660px;
  }
}

@media (min-width: 1380px) {
  .home-page .banner .text-wrapper .title {
    font-size: 78px;
  }
}

