.form-section {
  background: white;
  max-width: 700px;
  margin: 3vh auto 0;
  padding: 0 20px;
  max-height: 75vh;
}

.form-section .header {
  text-align: center;
}

.header {
  color: #005a09;
  font-family: Spectral;
}

.form {
  background: white;
  padding: 30px;
  border-radius: 20px;
  box-shadow: rgba(16, 35, 82, 0.02) 0 7px 50px 20px;
}

.form p {
  text-align: center;
  color: #005a09;
  font-weight: 700;
}

.form .title {
  margin: 10px 0;
}

.form-title {
  margin-bottom: 10px;
}

.form-group .input {
  margin: 8px 0 12px;
  width: 100%;
  border-radius: 4px;
  display: block;
  border: none;
  font-size: 14px;
  background: #eff1f6;
  padding: 12px 12px;
}

.form-group .input-error {
  padding: 8px 16px;
  border-radius: 4px;
  background-color: rgb(255, 192, 192);
  color: rgb(255, 34, 34);
}

.form-group .text-area {
  display: block;
  font-size: 14px;
  width: 100%;
  background: #eff1f6;
  border: none;
  border-radius: 4px;
  line-height: 1.3;
  resize: none;
  box-sizing: border-box;
  padding: 12px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Manrope", sans-serif;
}

.form-group .label {
  font-weight: 600;
}

.form button {
  margin-top: 30px;
  background: #e28277;
  width: 100%;
  color: white;
  padding: 16px 0;
  font-size: 16px;
  border-radius: 10px;
  border: none;
}

.form-section.link {
  text-align: center;
}

